.App {
  font-family: sans-serif;
  text-align: center;
}
.container {
  background-color: #ecd1f1a9;
  width: 100%;
  height: 100%;
}

.header h1 {
  text-shadow: 2px 2px #f149b1b2;
  height: 60px;
  color: rgba(0, 0, 0, 0.966);
  text-align: center;
  font-size: 2.4rem;
}
.header {
  position: relative;
  background-color: rgb(190, 120, 190);
  box-shadow: 4px 5px 5px #706f6f;
  padding-top: 1px;
  bottom: 20px;
}
.create {
  text-align: center;
  width: 100%;
  margin: 20px;
}
.create input {
  color: rgb(167, 74, 143);
  font-size: 1.5rem;
  padding: 20px;
  width: 300px;
  background-color: hsla(300, 28%, 80%, 0.445);
  border-top-style: hidden;
  border-right-style: hidden;
  border-left-style: hidden;
  border-bottom-style: groove;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.create textarea {
  padding: 20px;
  width: 300px;
  font-size: 1.3rem;
  background-color: hsla(300, 60%, 88%, 0.445);
  border-top-style: hidden;
  border-right-style: hidden;
  border-left-style: hidden;
  border-bottom-style: groove;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.create button {
  font-size: 2rem;
  width: 40px;
  height: 40px;
  border-radius: 80%;
  border: 2px solid;
  position: relative;
  background-color: rgb(190, 120, 190);
  bottom: 25px;
  left: 165px;
}
.create button:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.itemmain {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  margin: 10px;
}

.item {
  position: relative;
  width: 350px;

  padding-bottom: 35px;
  background-color: #f5b0e0;
  border: 3px solid #674a72;
  margin: 20px;
  border-top-style: hidden;
  border-right-style: hidden;
  border-left-style: hidden;
  border-bottom-style: hidden;
}
.item p {
  position: relative;
  font-size: 1.2rem;
  bottom: 20px;
  padding: 5px;
}

#itemheader {
  padding: 20px;
  position: relative;
  bottom: 22%;
  background-color: #b48dc2;
  box-shadow: 5px 5px 5px #674a72;
}
#deleteButton {
  position: absolute;
  left: 310px;
  bottom: 0%;
  width: 40px;
  font-size: 0.8rem;
  border-top-style: hidden;
  border-right-style: hidden;
  border-left-style: hidden;
  border-bottom-style: hidden;
  background-color: #b48dc2;
}

.footer {
  background-color: rgb(229, 194, 229);
  position: relative;
  width: 100%;
  bottom: -20%;
  text-align: center;
}
